
* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  overflow: hidden;
  font-family: Abel, sans-serif;
}
a {
  text-decoration: none;
}
a.about {
  position: absolute;
  top: 0;
  right: 0px;
  background: #f6f9ff;
  color: #ff4081;
  font-size: 21px;
  padding: 3px 10px;
}
.language-picker {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background: #191919;
  height: 100%;
  width: 300px;
  box-shadow: 0 -2px 22px rgba(0,0,0,0.4);
}
.language-picker .header {
  color: #999;
  padding: 20px 10px;
  font-size: 22px;
  background-color: #000;
  position: relative;
}
.no-select {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
}
.context-action {
  position: absolute;
  color: #999;
  top: 4px;
  right: 10px;
  font-size: 12px;
}
.back-to-all {
  color: #999;
}
.expand-list {
  display: none;
}
.word-list {
  overflow: hidden;
  width: 100%;
  -ms-flex: 1;
      flex: 1;
  position: relative;
}
.word-list .all-words,
.word-list .context {
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  transition: transform 0.25s ease;
}
.word-list .list {
  overflow-y: auto;
}
.word-list .all-words {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.word-list .all-words .line {
  color: #999;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.word-list .all-words .line .place {
  padding: 4px 4px 0 10px;
  font-size: 12px;
  color: #444;
  width: 40px;
}
.word-list .all-words .line .word {
  font-size: 1.17em;
  padding: 8px 0;
  -ms-flex: 1;
      flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}
.word-list .context {
  transform: translate(300px, 0);
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow: hidden;
  color: #fff;
}
.word-list .context .context-header {
  margin: 0;
  font-size: 1.17em;
  height: 70px;
  background-color: #111;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  padding: 0 10px;
}
.word-list .context .context-header a {
  line-height: 70px;
}
.word-list .context .context-header .context-word {
  color: #fff;
  -ms-flex: 1;
      flex: 1;
  display: block;
}
.word-list .context .line {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  border-bottom: 1px solid #333;
  padding: 10px 0;
}
.word-list .context .line .parts {
  padding-left: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.word-list .context .highlight {
  color: #ff4500;
}
.word-list .count {
  padding-right: 10px;
}
.word-list .context-visible {
  transform: translate(0, 0);
  transition: transform 0.25s ease;
}
.word-list .all-words.context-visible {
  transform: translate(-300px, 0);
}
#app {
  color: #999;
}
.scene-container {
  position: absolute;
  left: 300px;
  top: 0;
  bottom: 0;
  right: 0;
}
@media print {
.no-print,
  .no-print * {
    display: none !important;
}
}
@media (min-width: 670px) {
.line:hover {
    background-color: #333;
    color: #fff;
}
}
@media (max-width: 670px) {
.expand-list {
    display: block;
    height: 60px;
}
.scene-container {
    left: 0;
    bottom: 70px;
}
.list {
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}
.language-picker {
    width: 100%;
    height: 70px;
    bottom: 0;
}
.language-picker .header.context-visible {
    display: none;
}
.language-picker .context {
    transform: translate(100vw, 0);
}
.language-picker .all-words.context-visible {
    transform: translate(-100vw, 0);
}
.language-picker .context-visible {
    transform: translate(0, 0);
}
.language-picker.list-expanded {
    height: 100%;
}
}


