
.drop-click-container {
  display: inline-block;
  position: relative;
}
.drop-click-container .button-dropdown {
  display: inline-block;
  width: 0;
  height: 0;
  border: 4px solid transparent;
  border-top-color: #fff;
  opacity: 0.8;
  vertical-align: middle;
}
.drop-click-container .back-dropdown {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  opacity: 0;
}
.drop-click-container .drop-click-name {
  color: #fff;
  overflow: hidden;
  display: inline-block;
  vertical-align: bottom;
  height: 100%;
  white-space: nowrap;
}
.drop-click-container .drop-click-name.hover {
  color: #ff4081;
}
.drop-click-container .drop-click-name.hover .button-dropdown {
  border-top-color: #ff4081;
}


